html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'DM Sans', sans-serif;
  display: flex;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
}

button {
  font-size: 56px;
  font-weight: 700;
  text-align: center;
  border-radius: 75px;
  height: 100px;
  padding: 5px;
  margin: 20px;
}

.start {
  color: #007400;
  background-color: #19ce22;
}

.stop {
  color: #740000;
  background-color: #ce1919;
}

[disabled] {
  opacity: 0.5;
}
